.logo {
    color: rgb(255, 255, 255);
    font-size: 30px;
  }
  .pages ul li {
    font-size: 30px;
    justify-content: center;
  }
  nav {
    background-color: #7cbdd8;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 10px;
  }
  
  li {
    display: inline;
    margin-right: 20px;
  }
  
  li:last-child {
    margin-right: 0;
  }
  
  a {
    color: #fff;
    text-decoration: none;
  }
  
  a:hover {
    color: #d36fec;
  }
  
  /* Page Content */
  .container {
    max-width: 800px;
    margin: 20px auto;
    padding: 0 20px;
  }
  
  h2 {
    color: #333;
  }
  
  p {
    color: #666;
  }
  
  .home h2 {
    color: blueviolet;
  }
  
  .contact h2 {
    color: blueviolet;
  }
  
  .about h2 {
    color: blueviolet;
  }
  
  .name {
    color: white;
    font-size: 15px;
  }
  /* styles.css */

/* Global font styles */
body {
  font-family: 'Roboto', sans-serif; /* Example of using a Google Font */
  font-size: 0.5rem; /* Example of setting font size */
  font-weight: bold; /* Example of setting font weight */
  color: #100d0d; /* Example of setting font color */
}

/* Specific font styles for different elements */
h1 {
  font-family:'Times New Roman',serif ; /* Example of using a Google Font */
  font-size: 1.5em; /* Example of setting font size */
  font-weight: Extra Bold; /* Example of setting font weight */
  color: #905353; /* Example of setting font color */
}
h2 {
  font-family:'Times New Roman',serif ; /* Example of using a Google Font */
  font-size: 1rem; /* Example of setting font size */
  font-weight: Extra Bold; /* Example of setting font weight */
  color: #0d0a0a; /* Example of setting font color */
}

p {
  font-family: Georgia, serif; /* Example of using a fallback font */
  font-size: 1rem;
  line-height: 1.5; /* Example of setting line height */
}

.custom-font {
  font-family: 'MyFont', sans-serif; /* Example of using a locally defined font */
  font-size: 1.2rem;
  font-style: bold; /* Example of setting font style */
}


  