.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin-top: 0px; /* Use the full height of the viewport */
  }
  
  .form-container {
    width: 300px;
    margin-top: 0px;
    padding: 20px;
    border: 1px solid #6e0a6e;
    border-radius: 5px;
    background-color: #ced7e3;
  }
  
  .form-container h2 {
    margin-top: 0;
  }
  
  .form-container label {
    display: block;
    margin-bottom: 5px;
  }
  
  .form-container input[type="text"],
  .form-container input[type="password"] {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #8b97c7;
    border-radius: 3px;
  }
  
  .form-container button {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 3px;
    background-color: #d5dbe2;
    color: #1690ac;
    cursor: pointer;
  }
  
  .form-container button:hover {
    background-color: #0056b3;
  }
  
  .form-container p {
    text-align: center;
    margin-top: 10px;
  }