.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Use the full height of the viewport */
  }
  
  .form-container {
    width: 300px;
    padding: 20px;
    border: 1px solid #a91ec5;
    border-radius: 5px;
    background-color: #781855;
  }
  
  .form-container h2 {
    margin-top: 0;
  }
  
  .form-container label {
    display: block;
    margin-bottom: 5px;
  }
  
  .form-container input[type="text"],
  .form-container input[type="email"],
  .form-container input[type="password"] {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #54d2e0;
    border-radius: 3px;
  }
  
  .form-container button {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 3px;
    background-color: #007bff;
    color: #391555;
    cursor: pointer;
  }
  
  .form-container button:hover {
    background-color: #c25424;
  }
  
  .form-container p {
    text-align: center;
    margin-top: 10px;
  }
  